import axios from "axios";

export function postLogin(user) {
  return axios.post("http://eatbana-dev.us-east-2.elasticbeanstalk.com/api/users/login", user);
}

export function postRegister(restaurant) {
  return axios.post("http://eatbana-dev.us-east-2.elasticbeanstalk.com/api/restaurants", restaurant);
}